import FirebaseRepository from "./firebaseRepository";
import { skills, competencies, education, experience } from "app/constants";
import en from "app/translations/resources/en.json"
import es from "app/translations/resources/es.json"

const backup = {
    skills,
    competencies,
    education,
    experience,
    ...configTranslation({ en, es })
}

function configTranslation(data){
    return {
        en: {
            translation: data.en
        },
        es: {
            translation: data.es
        }
    }
}

class Data {

    constructor(){
        this.repository = new FirebaseRepository();
        this.session = window.sessionStorage;
    }

    getDB(){

        if(this.db == null){
            this.db = this.repository.getCollection("mounted");
        }

        return this.db;
    }

    async getManager(modelName, convertToArray = false){

        //return data from session storage
         if(this.session.getItem(modelName)){
            return JSON.parse(this.session.getItem(modelName));
         }
 
         let data = await this.getDB();

         //return data from firebase
         if(data){

            if(convertToArray){
                if(!Array.isArray(data))
                  data[modelName] = Object.values(data[modelName])
            }
            this.session.setItem(modelName, JSON.stringify(data[modelName]));
         } else {
             //return data from local files
             return backup[modelName];
         }

         return data[modelName];
    }

    async getSkills(){

        let skills = await this.getManager("skills");

        //map
        return {
            languages: skills.languages,
            frameworks: skills.frameworks,
            database: skills.database,
            others: skills.others
        }
    }

    async getCompetencies(){
        return await this.getManager("competencies", true)
    }

    async getExperience(){
        return await this.getManager("experience", true)
    }

    async getEducation(){
        return await this.getManager("education", true)
    }

    async getRootData(){

        return {
            experience: await this.getExperience(),
            education: await this.getEducation(),
            translation: await this.getResourcesTranslation(),
            competencies: await this.getCompetencies(),
            skills: await this.getSkills()
        }

    }

    async getResourcesTranslation(){

        const translations = await this.getManager("translations");
        return configTranslation(translations)
    }

    
}

export default new Data();