import React from 'react'
import PropTypes from 'prop-types'

const footer = ({ name }) => {
    return(
        <footer className="text-center ">
                <p className="m-0 pb-3">©Copyright { new Date().getFullYear() }. { name }</p>
        </footer>
    );
}

footer.propTypes = {
    name : PropTypes.string.isRequired
}

export default footer;