import { SET_IP_CLIENT, SET_DEVICE } from "./../constants";

export default (state, { type, payload }) => {

    switch(type){
        case SET_IP_CLIENT: return {...state, ip: payload} 
        case SET_DEVICE: return {...state, device: payload} 
        default: 
          return {
            sessionId: Math.random().toString(36),
            ip: "",
            timeStart: new Date(),
            device: {},
            ...state
          };
    }
}