import React from 'react';
import { Grid, Row, Col } from 'react-flexbox-grid';
import { Progress } from "app/components";
import { withTranslation } from 'react-i18next';

function Competencies ({ items, t }) {

    return (
        <div>
            <Grid fluid>
                <Row>
                    {
                        items.map((item, idx) => (
                            <Col key={item.label} xs={12} md={6}>
                                <div className="skill mb-3">
                                    <Progress 
                                      color="secondary"
                                      percentage={item.progress}
                                      icon={item.icon}
                                      text={t(item.label)}/>
                                </div>
                            </Col>
                        ))
                    }
                </Row>
            </Grid>
        </div>
    );
}

export default withTranslation()(Competencies);

