import { SET_THEME_CLIENT, SET_LANG_CLIENT, SET_RATING, SET_CV } from "./../constants";
import { getInitialLang  } from "app/translations"

export default (state, { type, payload }) => {

    switch(type){
        case SET_THEME_CLIENT: return {...state, theme: payload, lastAction: SET_THEME_CLIENT} 
        case SET_LANG_CLIENT: return {...state, lang: payload, lastAction: SET_LANG_CLIENT} 
        case SET_RATING: return {...state, rating: payload, lastAction: SET_RATING} 
        case SET_CV: return {...state, cvDownloded: payload, lastAction: SET_CV} 
        default: 
          return {
            theme: "dark",
            lang: getInitialLang(),
            cvDownloded: false,
            rating: 0,
            lastAction: null,
            ...state
          };
    }
}