import React from "react";
import Icon from '@material-ui/core/Icon';

export default (props) => {
    return (
        <>
            {
                props.text &&
                <h5>{props.text}</h5>
            }
            <div className="d-flex">
                {
                    props.icon &&
                    <div className="mt-n1 mr-2">
                        {
                            props.icon.includes("fa-") ? <em className={ props.icon }/> : <Icon>{props.icon}</Icon>
                        }
                    </div>
                }
                <div className="w-100">
                    <div className="progress">
                        <div
                            className={`progress-bar progress-bar-striped progress-bar-animated w-${props.percentage} bg-${props.color}`}
                            role="progressbar"
                            aria-valuemin="0"
                            aria-valuemax="100">{ props.percentage && <span className="font-weight-bold">{`${props.percentage}%`}</span>}</div>
                    </div>
                </div>
            </div>
        </>
    );
}