import React, { useEffect } from "react";
import { Snackbar, IconButton } from '@material-ui/core';
import CloseIcon from '@material-ui/icons/Close';

const AUTO_HIDE = 4000;

export default ({ message, onClose }) => {

    useEffect(() => {
        setTimeout(() => {
            onClose();
        }, AUTO_HIDE );
    }, [onClose]);

    return(
        <Snackbar
                anchorOrigin={{
                    vertical: 'top',
                    horizontal: 'right',
                }}
                open={ true }
                ContentProps={{
                    'aria-describedby': 'message-id',
                }}
                message={<span id="message-id">{ message }</span>}
                action={[
                    <IconButton
                        key="close"
                        aria-label="Close"
                        color="inherit"
                        onClick={() => onClose()}
                    >
                        <CloseIcon />
                    </IconButton>,
                ]}
            />
    );
}
