import React, { useState } from 'react'

import headerImage from 'app/assets/img/gif/header.gif'
import styles from 'app/assets/css/header.module.css'
import { languages as langs, themes } from "app/constants"

import {IconButton, Toolbar, AppBar} from '@material-ui/core';

import Select from "app/components/select"

import Brightness4Icon from '@material-ui/icons/Brightness4';
import Brightness7Icon from '@material-ui/icons/Brightness7';
import CodeIcon from '@material-ui/icons/Code';

import { useSelector, useDispatch } from 'react-redux'
import { withTranslation } from 'react-i18next';
import { setLanguageAction, setThemeAction } from "app/store/actions"

let languages = [
    langs.ENGLISH,
    langs.SPANISH
]

function Header({ t, i18n }) {

    const dispatch =  useDispatch();
    const app = useSelector(state => state.app);

    const [language, setLanguage] = useState(app.lang);
    const [theme, setTheme] = useState(app.theme);

    function setGlobalLanguage(event){
        let lang = event.target.value;
        
        setLanguage(lang)
        i18n.changeLanguage(lang);
        dispatch(setLanguageAction(lang));
    }

    function setGlobalTheme(){

        let th = theme === themes.LIGHT ? themes.DARK : themes.LIGHT;
        setTheme(th)
        dispatch(setThemeAction(th));
    }

    return (
        <>
            <AppBar className="app-bar">
                <Toolbar>
                    <CodeIcon />
                    <div className="ml-auto d-flex">
                        <div className="mr-2">
                        <IconButton 
                            onClick={ setGlobalTheme }
                            className="text-white"
                            component="span">
                                {
                                    theme === themes.LIGHT ? <Brightness4Icon style={{ fontSize: 30 }} /> :
                                    <Brightness7Icon style={{ fontSize: 30 }} />
                                }
                        </IconButton>
                        </div>
                        <div className="theme-dark mt-2">
                            <Select
                                label={ t('language') }
                                change={ setGlobalLanguage }
                                items={languages}
                                value={language}/>
                        </div>
                    </div>
                </Toolbar>
            </AppBar>
            <div
                className={`${styles.page_header} ${styles.header_filter}`}
                data-parallax="true"
                style={{ background: `url(${headerImage})` }}>
            </div>
        </>
    );

}

export default withTranslation()(Header);