import React, { useState, useEffect } from "react";
//import me from 'app/assets/img/me.jpg'
import cv from "app/assets/files/HaroldCV.pdf";
import { withTranslation } from 'react-i18next';
import { Button, Slide } from '@material-ui/core';
import DownloadIcon from '@material-ui/icons/CloudDownload';
import { useSelector, useDispatch } from "react-redux";
import SnackBar from "app/components/snackBar";
import { setCV } from "app/store/actions";
import FirebaseRepository from "app/api/firebaseRepository"

let animationEntryTime = 3000;
const repository = new FirebaseRepository();

function Profile ({ img, t }) {

    const phraseTranslation = t('phrase');
    const client = useSelector(state => state.client);
    const dispatch = useDispatch();

    const downloadCv = () => {
        setSnack(true);

        setTimeout(() => {
            repository.download(process.env.REACT_APP_FB_CV, cv);
            dispatch(setCV(true));
        }, 500)
    }

    let [snack, setSnack] = useState(false);
    let [phrase, setPhrase] = useState({
        text: "",
        index: 0,
        animationCompleted: false
    });

    useEffect(() => {

        if (phrase.animationCompleted) {
            setPhrase({
                ...phrase,
                index: phraseTranslation.length,
            })
        }

    }, [client.lang])

    useEffect(() => {

        let i = setTimeout(() => {

            if (phrase.text != phraseTranslation) {
                setPhrase({
                    ...phrase,
                    text: phraseTranslation.substring(0, phrase.index),
                    index: phrase.index + 1,
                    animationCompleted: phrase.index == phraseTranslation.length
                })
            }

        }, 45);

        return () => {
            clearTimeout(i)
        }
    });

    return (
        <>
            <div className="profile-image d-flex justify-content-center move-img-profile">
            <Slide timeout = {animationEntryTime} direction="down" in={true}>
                <img src={img} className="round" alt="me" />
            </Slide>
            </div>
            <div className="text-center my-3">
            <Slide timeout = {animationEntryTime} direction="left" in={true}>
                <p className="h1 font-weight-bold title">Harold Cordero</p>
            </Slide>
                <p>{t('position')}</p>
            </div>
            <p className="text-justify description-paragraph mx3 mb2">
                {phrase.text}
            </p>
            <div className="d-flex justify-content-center mb-3">
                <Button
                    onClick={downloadCv}
                    variant="contained"
                    color="primary"
                    startIcon={<DownloadIcon />}>
                    {t('download')}
                </Button>
            </div>

            {
                snack && 
                      <SnackBar 
                        onClose={ () => setSnack(false) }
                        message={t('download-started')}/>
            }
            
        </>
    )
}

export default withTranslation()(Profile);
