import React, { useState, useEffect } from 'react';
import me from 'app/assets/img/me.jpg'
import { useSelector, useDispatch } from 'react-redux'
import { Container } from "app/layouts/container";
import { Competencies, Skills, Contact, Footer, Rating, Profile, Header } from "app/containers";
import { Timeline, Preloader } from "app/components";
import { getOffsetSection, getClientIp } from "app/utils";
import { setIpClientAction, setDeviceAction } from "app/store/actions";
import DeviceDetector from "device-detector-js";
import api from "app/api";
import { withTranslation } from 'react-i18next';
import translationManager from 'app/translations';

//translationManager();
function App ({ t }) {

  const app = useSelector(state => state.app);
  const dispatch = useDispatch();

  const [openRating, setOpenRating] = useState(false);
  const [loading, setLoading] = useState(true);
  const [information, setInformation] = useState({
    education: [],
    experience: [],
    competencies: []
  });

  useEffect(() => {

    const { device, os } = new DeviceDetector().parse(window.navigator.userAgent);

    getClientIp().then((ip) => {
      dispatch(setIpClientAction(ip));
    });

    dispatch(setDeviceAction({
      device,
      os
    }));

    api.getRootData().then(data => {

      translationManager(data.translation);
      
      setInformation({
        education: data.education,
        experience: data.experience,
        competencies: data.competencies,
        skills: data.skills
      });

      setTimeout(() => {
        setLoading(false);
      }, 1000);

    })

   window.addEventListener("scroll", onScroll);

    return () => {
      window.removeEventListener("scroll", onScroll);
    }
  }, []);

  function onScroll(event) {

    let isSectionEducation = getOffsetSection(document.getElementById('experience')) > 100;

    if (isSectionEducation) {
      setOpenRating(true);
      window.removeEventListener("scroll", onScroll);
    }
  }

  return (
    loading ? <Preloader /> :
      <div className={`theme-${app.theme}`}>
        <div className="app-container">
          <Header />
          <Container className="first-container">
            <Profile img={ me }/>
          </Container>
          <h1 className="title text-center">{t('skills')}</h1>
          <Container>
            <Competencies items={ information.competencies }/>
          </Container>
          <h1 className="title text-center">{t('competencies')}</h1>
          <Container className="p-0">
            <Skills skills={information.skills}/>
          </Container>
          <h1 className="title text-center">{t('education')}</h1>
          <Container className="p-0">
            <Timeline items={information.education} />
          </Container>
          <h1 className="title text-center">{t('experience')}</h1>
          <Container id="experience" className="p-0">
            <Timeline items={ information.experience } />
          </Container>
          <h1 className="title text-center">{t('contactme')}</h1>
          <Container className="p-3">
            <Contact />
          </Container>
          <Footer name="Harold Cordero" />
        </div>

        <Rating
          onClose={() => setOpenRating(false)}
          open={openRating} />
      </div>
  )
}

export default withTranslation()(App);
