import * as app from 'firebase';

export default class FirebaseRepository {

  async getCollection(collectionName) {
    let markers = {};

    try {

      let querySnapshot = await app.firestore().collection(collectionName).get()

      querySnapshot.docs.forEach(doc => {
        markers[doc.id] = doc.data();
      });

    } catch (e) {

      markers = null;

    }

    return markers;
  }

  set(collectionName, docName, data) {
    app.firestore().collection(collectionName).doc(docName).set(Object.assign({}, data)).then(function () {
      console.log("Document successfully written!");
    })
      .catch(function (error) {
        console.error("Error writing document");
      });
  }

  add(collectionName, data) {
    // Add a new document with a generated id.
    app.firestore().collection(collectionName).add(data)
  }

  download(refUrl, contigencieUrl = null) {
    let storage = app.storage();

    var anchor=document.createElement('a');
    anchor.setAttribute('target','_blank');
    document.body.appendChild(anchor);
      
    // Create a reference from a Google Cloud Storage URI
    storage.refFromURL(refUrl).getDownloadURL().then(url => {
      console.log(url)
      anchor.setAttribute('href',url);
    	anchor.click();
    	
    }).catch(() => {
      if(contigencieUrl){
        anchor.setAttribute('href',contigencieUrl);
    	  anchor.click();
      }
    }).finally(() => {
      anchor.parentNode.removeChild(anchor);
    })

  }

}