import React from "react";
import css from "app/assets/css/timeline.module.css";
import { withTranslation } from 'react-i18next';

function Timeline({ items, t }){

    return (
        <div className="container">
            <div className="row">
                <div className="col-md-12">
                    <div className={`${css.main_timeline} mb-3`}>
                        {
                            items.map(item => {
                                return (
                                    <div key={ item.title } className={css.timeline}>
                                        <span className={`${css.icon} ${item.icon}`}></span>
                                        <div className={`${css.timeline_content} p-3`}>
                                            <h3 className={`${css.title} mt-3`}>{item.year && item.year + ' -'} {t(item.title)}</h3>
                                            <span>{t(item.place)}</span>
                                            <p className={`${css.description} mt-1 text-justify`}>{t(item.description)}</p>
                                            {
                                                item.action &&
                                                <div className="cursor-pointer">
                                                    <em className="fas fa-eye" /> <span>{t('see')}</span>
                                                </div>
                                            }
                                        </div>
                                    </div>

                                )
                            })
                        }
                    </div>
                </div>
            </div>
        </div>
    );
}

export default withTranslation()(Timeline);