import React, { useState } from "react";
import { Button, Icon } from '@material-ui/core';
import { withTranslation } from 'react-i18next';
import emailjs from 'emailjs-com';
import { ValidatorForm, TextValidator } from 'react-material-ui-form-validator';
import { Dialog, Loading } from "app/components"

const form = React.createRef();

function Contact({ t }) {

    const [state, setState] = useState({
        email: "",
        affair: "",
        message: ""
    });

    const [isLoading, setIsLoading] = useState(false);

    const [dialog, setDialog] = useState({
        open: false,
        message: t('email.success'),
        variant: "success"
    });

    const setValue = (id, event) => {
        setState({
            ...state,
            [id]: event.target.value
        });
    }

    function sendEmail(params) {

        setIsLoading(true);

        emailjs.send(
            process.env.REACT_APP_EMAIL_SERVICEID, process.env.REACT_APP_EMAIL_TEMPLATEID,
            params,
            process.env.REACT_APP_EMAIL_USERID)
            .then((result) => {

                setIsLoading(false);

                setDialog({
                    ...dialog,
                    open: true,
                    variant: "success",
                    message: t('email.success')
                })
            }, (error) => {

                setIsLoading(false);
                
                setDialog({
                    ...dialog,
                    open: true,
                    variant: "danger",
                    message: t('email.danger')
                })
            });
    }

    return (

        <div className="container">
            <ValidatorForm
                ref={form}
                onSubmit={() => sendEmail(state)}
            >
                <div className="row">
                    <div className="col-md-6 col-sm-12">
                        <iframe className="iframe-map"
                            style={{ width: "100%", height: "100%" }}
                            src="https://www.google.com/maps/embed?pb=!1m18!1m12!1m3!1d3783.5488366019167!2d-69.86951668036299!3d18.504084374509535!2m3!1f0!2f0!3f0!3m2!1i1024!2i768!4f13.1!3m3!1m2!1s0x8eaf88871937e0e1%3A0xfff5161dddf4618!2sCalle+7%2C+Santo+Domingo+Este!5e0!3m2!1ses-419!2sdo!4v1549822623258"
                            frameBorder="0"
                            title="Mi direccion"
                            allowFullScreen></iframe>
                    </div>
                    <div className="col-md-6 col-sm-12">
                        <div className="row">
                            <div className="col">
                                <TextValidator
                                    validators={['required', 'isEmail']}
                                    errorMessages={[t('validations.required'), t('validations.email')]}
                                    value={state.email}
                                    onChange={(event) => setValue('email', event)}
                                    required
                                    id="email"
                                    label="Email"
                                    fullWidth
                                    margin="normal"
                                    variant="outlined"
                                />
                            </div>
                        </div>
                        <div className="row">
                            <div className="col">
                                <TextValidator
                                    validators={['required']}
                                    errorMessages={[t('validations.required')]}
                                    value={state.affair}
                                    onChange={(event) => setValue('affair', event)}
                                    id="subject"
                                    label={t('affair')}
                                    fullWidth
                                    margin="normal"
                                    variant="outlined"
                                    required
                                />
                            </div>
                        </div>
                        <div className="row">
                            <div className="col">
                                <TextValidator
                                    validators={['required']}
                                    errorMessages={[t('validations.required')]}
                                    id="message"
                                    value={state.message}
                                    onChange={(event) => setValue('message', event)}
                                    label={t('message')}
                                    multiline
                                    rows="4"
                                    fullWidth
                                    margin="normal"
                                    variant="outlined"
                                    required
                                />
                            </div>
                        </div>
                        <div className="row">
                            <div className="col mt-2">
                                <Button
                                    onClick={() => form.current.submit()}
                                    className="w-100"
                                    endIcon={<Icon>send</Icon>}
                                    variant="contained"
                                    color="primary">
                                    {t('send')}
                                </Button>
                            </div>
                        </div>
                    </div>
                </div>
            </ValidatorForm>

            <Dialog 
               variantIcon={ dialog.variant }
               title={ dialog.message }
               actions
               onClose={ () => setDialog(false) }
               open={ dialog.open }/>
            
            <Loading open={ isLoading }/>
            
        </div>
    )
}

export default withTranslation()(Contact);
