import React from 'react'
import PropTypes from 'prop-types'

export const Container = (props) => {
    return (
        <div {...(props.id && {id: props.id})} className={`d-flex justify-content-center`}>
            <div className={`w-85 my-4 paper ${props.className}`}>
                {props.children}
            </div>
        </div>
    );
}

Container.propTypes = {
    className: PropTypes.string
}

Container.defaultProps = {
    className: ""
}