import React from "react";
import { withTranslation } from 'react-i18next';
import { Dialog, DialogContent, DialogTitle, DialogContentText, DialogActions, Button, Slide } from '@material-ui/core';

const Transition = React.forwardRef(function Transition(props, ref) {
    return <Slide direction="up" ref={ref} {...props} />;
});

function DialogComponent(props) {
    

    let variantIcon = "";
    const { t } = props;

    switch(props.variantIcon){
        case "success": variantIcon = "fas fa-exclamation-circle text-success";
           break;
        case "danger": variantIcon = "fas fa-times-circle text-danger";
           break;
        default:
            break;
    }
    return (
        <Dialog
            open={props.open}
            TransitionComponent={Transition}
            keepMounted
            onClose={props.handleClose}
            aria-labelledby="alert-dialog-slide-title"
            aria-describedby="alert-dialog-slide-description"
        >
            {
                (props.variantIcon || props.icon) &&
                   <em className={`display-4 text-center mt-4 ${props.icon} ${variantIcon}`}/>
            }

            {
                props.title &&
                <DialogTitle className="text-justify">{props.title}</DialogTitle>
            }
            {
                props.children &&
                <DialogContent>
                    <DialogContentText>
                        {
                            props.children
                        }
                    </DialogContentText>
                </DialogContent>
            }
            {
                props.actions &&
                <DialogActions>
                    <Button onClick={() => props.onClose() } color="primary">
                        { t('accept') }
                    </Button>
                </DialogActions>
            }
        </Dialog>
    );
}

export default withTranslation()(DialogComponent);