import React, { useState, useEffect } from 'react';
import { Modal, Fade } from '@material-ui/core';
import Rating from '@material-ui/lab/Rating';
import StarBorderIcon from '@material-ui/icons/StarBorder';
import { withTranslation } from 'react-i18next';
import { useDispatch } from 'react-redux';
import { setRating } from "app/store/actions/index";
import { Dialog } from "app/components"

const labels = {
    1: 'useless',
    2: 'poor',
    3: 'Ok',
    4: 'good',
    5: 'excellent',
};

const VOTED = "voted"

function RatingComponent (props){

    const { t } = props;
    const dispatch = useDispatch();

    const [value, setValue] = useState(-1);
    const [hover, setHover] = useState(-1);
    const [openDialog, setOpenDialog] = useState(false);

    const puntuation = hover !== -1 ? hover : value;
    const open = props.open && sessionStorage.getItem(VOTED) == null;

    useEffect(() => {

        if (value > 0) {

            dispatch(setRating(value));
            sessionStorage.setItem(VOTED, true)

            setTimeout(() => {
                props.onClose();

                setOpenDialog(true)

                setTimeout(() => {
                    setOpenDialog(false)
                }, 2000);

            }, 1000);

        }
    }, [value]);

    return (
        <>
            <Modal
                className="d-flex align-items-center justify-content-center"
                onClose={() => props.onClose()}
                open={open}>
                <Fade in={props.open}>
                    <div className="bg-white outline-0 mx-5 p-3 rounded">
                        <div className="text-center">
                            <h3>{t('rating.title')}</h3>
                        </div>
                        <div className="d-flex justify-content-center">
                            <Rating
                                name="hover-feedback"
                                value={value}
                                precision={0.5}
                                onChange={(event, newValue) => {
                                    setValue(newValue);
                                }}
                                onChangeActive={(event, newHover) => {
                                    setHover(newHover);
                                }}
                                emptyIcon={<StarBorderIcon fontSize="inherit" />} />
                        </div>
                        <div className="text-center">
                            <span>{t(labels[Math.round(puntuation)])}{(Number.isInteger(puntuation) && puntuation > 0) && "+"}</span>
                        </div>
                    </div>
                </Fade>
            </Modal>

            <Dialog 
               variantIcon="success"
               open={openDialog}
               title={t('thanks.puntuation')}/>
        </>
    );
}

export default withTranslation()(RatingComponent);