import React, { useEffect, useState } from "react";
import { TabContainer, TabPanel } from "app/components/tabs";
import { Progress } from "app/components";
import { withTranslation } from 'react-i18next';
import { Grid, Row, Col } from 'react-flexbox-grid';
import data from "app/api";


function Skills ({ t, items }) {

    const [skills, setSkills] = useState({});

    useEffect(() => {

        data.getSkills().then(data => {
          setSkills(data);
        })

    }, []);

    return (
        <TabContainer
            tabs={Object.keys(skills)}>
            {
                Object.keys(skills).map(((skill, index) => {

                    let items = skills[skill];
                    return (
                        <TabPanel
                            key={`${skill}${index}`}
                            index={index}>
                            <Grid fluid>
                                <Row>
                                    {
                                        items.map((item, idx) => (
                                            <Col key={`${item.skill}${idx}`} className="mb-3" xs={12} md={6}>
                                                <Progress
                                                    color="secondary"
                                                    icon={item.icon}
                                                    percentage={item.progress}
                                                    text={t(item.skill)} />
                                            </Col>
                                        ))
                                    }

                                </Row>
                            </Grid>
                        </TabPanel>
                    )
                }))
            }
        </TabContainer>
    );
}

export default withTranslation()(Skills);